.login {
  width: 363px;
  margin: 20vh auto 0;
  padding: 50px 0;
  background-color: rgba(255, 255, 255, 0.85);
  border-radius: 4px;
  text-align: center;
  font-size: 14px;
}
.login .logo-wrap {
  margin-bottom: 40px;
  text-align: center;
}
.login .logo-wrap .logo {
  display: inline-block;
  width: 180px;
  height: 60px;
  margin: 0;
  margin-bottom: 6px;
  background: url(data:image/png;base64,) no-repeat;
  background-size: auto 100%;
  background-position: 50% 50%;
}
.login .logo-wrap .text {
  color: #757c80;
}
.login .input-wrap {
  text-align: center;
}
.login .input-wrap .input-filed {
  margin: 0px auto;
  width: 300px;
  border: solid 1px #dfebf2;
  border-bottom: none;
  border-radius: 4px 4px 0 0;
  background: #fff;
}
.login .input-wrap .input-filed:last-child {
  border-bottom: solid 1px #dfebf2;
}
.login .input-wrap .input-filed input {
  display: block;
  width: 290px;
  height: 40px;
  line-height: 40px;
  padding: 0;
  padding-left: 10px;
  border: none;
  border-radius: 4px;
  font-size: 16px;
}
.login .error-wrap {
  width: 300px;
  height: 12px;
  line-height: 12px;
  margin: 0 auto 3px;
  color: red;
  text-align: left;
}
.login .button-wrap {
  width: 300px;
  height: 40px;
  margin: 0 auto;
  font-size: 0;
}
.login .button-wrap button {
  display: inline-block;
  width: 120px;
  height: 40px;
  line-height: 40px;
  margin: 0;
  padding: 0;
  border: none;
  border-radius: 4px;
  background-color: #25c6ff;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
}
.login .button-wrap .register {
  margin-left: 20px;
  background-color: aquamarine;
}
.login .background-image {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: url(/static/img/bg_01.ee154ef.jpg) no-repeat;
  background-size: cover;
  background-position: 50% 0;
  z-index: -1;
  -webkit-filter: blur(2px);
          filter: blur(2px);
}
.toast-title {
  font-weight: bold;
}
.toast-message {
  -ms-word-wrap: break-word;
